                <span></span>
            </div>
        </section>
        <section class="section section-skew">
            <div class="container" v-if="userExists">
                <b-overlay :show="loading || autoToggleRxUser == undefined">
                    <card shadow class="card-profile mt--300" no-body>
                        <div class="px-4">
                            <div class="row justify-content-center">
                                <div class="col-lg-3 order-lg-2 hover-big-shadow">
                                    <div class="card-profile-image">
                                        <a href="#">
                                            <img
                                                v-lazy="`https://a.ppy.sb/${autoToggleRxUser.id}`"
                                                class="rounded-circle"
                                                width="400px"
                                            />
                                        </a>
                                    </div>
                                </div>
                                <div class="col-lg-4 order-lg-3 text-lg-right align-self-lg-center">
                                    <div class="card-profile-actions py-4 mt-lg-0">
                                        <base-button
                                            type="info"
                                            size="sm"
                                            class="mr-4"
                                        >Add as friend</base-button>
                                        <base-button
                                            type="default"
                                            size="sm"
                                            class="float-right"
                                        >Block</base-button>
                                    </div>
                                    <div>
                                        <span class="align-top mr-2">toggle rx:</span>
                                        <base-switch id="input-sm" v-model="rxStat" size="sm"></base-switch>
                                    </div>
                                </div>
                                <div class="col-lg-4 order-lg-1">
                                    <div
                                        class="card-profile-stats d-flex justify-content-center"
                                        v-if="autoToggleRxUser[strMode]"
                                    >
                                        <div
                                            v-for="(v,k) in {
                                            pp: autoToggleRxUser[strMode].pp, 
                                            accuracy: smallerFloatHtml(`${autoToggleRxUser[strMode].accuracy.toFixed(2)}%`),
                                            'Play Count': autoToggleRxUser[strMode].playcount,
                                            //TTH: autoToggleRxUser[strMode].total_hits,
                                            }"
                                            :key="`user-stat-${k}`"
                                        >
                                            <span class="heading" v-html="v"></span>
                                            <span class="description" v-html="k"></span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="text-center mt-5" v-if="autoToggleRxUser[strMode]">
                                <h3>
                                    {{autoToggleRxUser.username}}
                                    <small
                                        class="font-weight-light"
                                    >, #{{autoToggleRxUser[strMode].global_leaderboard_rank || 0}}</small>
                                </h3>
                                <div class="h6 font-weight-300">
                                    <i class="ni location_pin mr-2"></i>
                                    <gb-flag
                                        :code="autoToggleRxUser.country"
                                        size="small"
                                        iconPath="assets/flags"
                                    />
                                    #{{autoToggleRxUser[strMode].country_leaderboard_rank || 0}}
                                </div>
                                <div class="h6 mt-4">
                                    <i class="ni business_briefcase-24 mr-2"></i>
                                    Last seen at {{moment(autoToggleRxUser.latest_activity).fromNow()}}
                                </div>
                                <div>
                                    <i class="ni education_hat mr-2"></i>
                                    registered at {{moment(autoToggleRxUser.registered_on).format('LLL')}}
                                </div>
                            </div>
                            <div class="mt-5 py-5 border-top text-left">
                                <p v-html="userpage"></p>
                                <!-- <div class="row justify-content-center text-center">
                                <div class="col-lg-9">
                                    <a href="#">Show more</a>
                                </div>
                                </div>-->
                                <b-tabs v-model="numericMode" class="nav-wrapper" pills fill>
                                    <b-tab>
                                        <span slot="title" class="nav-link-icon d-block">std</span>
                                    </b-tab>
                                    <b-tab>
                                        <span slot="title" class="nav-link-icon d-block">taiko</span>
                                    </b-tab>
                                    <b-tab>
                                        <span slot="title" class="nav-link-icon d-block">catch</span>
                                    </b-tab>
                                    <b-tab>
                                        <span slot="title" class="nav-link-icon d-block">mania</span>
                                    </b-tab>
                                </b-tabs>
                            </div>
                        </div>
                    </card>
                </b-overlay>
            </div>
            <div class="container" v-else>
                <article>user not exists</article>
            </div>
        </section>
        <section class="section section-shaped mt-0" v-if="userExists && !loading">
            <div class="shape shape-style-1 bg-gradient-primary shape-skew">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
            </div>
            <div class="container">
                <bp-card :id="id" :rx="rxStat" :mode="numericMode" />
            </div>
        </section>
        <section class="section section-shaped my-0" v-if="userExists && !loading">
            <div class="shape shape-style-1 bg-gradient-warning shape-skew">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
            </div>
            <div class="container">
                <recent-card :id="id" :rx="rxStat" :mode="numericMode" />
            </div>
        </section>
    </div>
</template>
<script>
const axios = require("axios");

import bbCodeParser from "js-bbcode-parser";
const defaultCodes = [...bbCodeParser.codes];

import GoTop from "@inotom/vue-go-top";
import BpCard from "@/views/components/sb/BPCard";
import RecentCard from "@/views/components/sb/RecentCard";
const defaultUser = {
    id: undefined,
    name: "peppy"
};
export default {
    data: () => ({
        user: {},
        userRx: {},
        userpageRaw: "",
        userpage: "",
        id: undefined,
        selectedMode: 0,
        logger: true,
        loading: true,
        userExists: true
    }),
    watch: {
        async handle() {
            await this.initUserId();
            await this.switchUser();
        },
        rx() {
            // await this.initUserId();
            this.switchUser();
        },
        $route(){
            // this.updateVuex();
        },
    },
    props: {
        handle: {
            type: String,
            default: -1
        },
        mode: {
            type: String,
            default: "std"
        },
        rx: {
            type: String,
            default: ""
        }
    },
    beforeCreate() {
        const l2Parser = {};
        l2Parser.__proto__ = bbCodeParser.__proto__;
        l2Parser.setCodes({
            "\\[center\\](.+?)\\[/center\\]":
                '<div class="text-center">$1</div>'
        });
        const newCodes = l2Parser.codes;
        bbCodeParser.codes = [...defaultCodes, ...newCodes];
    },
    computed: {
        numericMode: {
            get() {
                switch (this.mode) {
                    case "std":
                    case "osu":
                        return 0;
                    case "taiko":
                        return 1;
                    case "catch":
                    case "ctb":
                        return 2;
                    case "mania":
                        return 3;
                    default:
                        return 0;
                }
            },
            set(newVal) {
                this.$router.push({
                    name: "users",
                    params: {
                        handle: this.id,
                        mode: this.toStrMode(newVal),
                        rx: this.rx ? this.rx : undefined
                    }
                });
            }
        },
        autoToggleRxUser: {
            get() {
                return this.rxStat ? this.userRx : this.user;
            },
            set(user) {
                if (this.rxStat) {
                    this.userRx = user;
                } else {
                    this.user = user;
                }
            }
        },
        strMode() {
            return this.toStrMode();
        },
        rxStat: {
            get() {
                return Boolean(this.rx == "rx");
            },
            set(newVal) {
                if (typeof newVal == "object") return;
                this.$router.push({
                    name: "users",
                    params: {
                        handle: this.id,
                        mode: this.mode,
                        rx: newVal ? "rx" : undefined
                    }
                });
            }
        }
    },
    created() {
        this.log({
            handle: this.handle,
            mode: this.mode,
            rx: this.rxStat,
            numericMode: this.numericMode
        });
        this.init();
        
        this.storeShit('sbStore',{});
        const ref = this.getShit('sbStore')
        ref.sb = this.$props;
        this.storeShit('sbStore',ref);
        this.log(this.getShit('sb'));
        
    },
    components: {
        BpCard,
        RecentCard,
        GoTop
    },
    methods: {
        async init() {
            // this.loading = true;
            await this.initUserId();
            await this.initUser();
            await this.initUserpage();
            this.loading = false;
        },

        async initUserId() {
            const apiBase = this.$root.rippleApiBase;

            if (!isNaN(this.handle)) this.id = parseInt(this.handle);
            else {
                try {
                    const result = await axios({
                        url: `${apiBase}/users/whatid`,
                        params: {
                            name: this.handle
                        }
                    }).then(res => res.data);
                    this.id = result.id;
                    this.userExists = true;
                } catch (error) {
                    this.log(error);
                    this.id = undefined;
                    this.userExists = false;
                    // this.loading = false;
                }
            }
        },
        async initUser() {
            const apiBase = this.$root.rippleApiBase;
            return axios({
                url: `${apiBase}/users/${this.rxStat ? "rx" : ""}full`,
                params: {
                    id: this.id
                }
            })
                .then(res => res.data)
                .then(user => {
                    user.rx = this.rxStat;
                    this.autoToggleRxUser = user;
                    this.userExists = true;
                })
                .catch(error => {
                    this.log(error);
                    this.userExists = false;
                    return {};
                });
        },
        async initUserpage() {
            const apiBase = this.$root.rippleApiBase;
            this.userpageRaw = "";
            this.userpage = "";

            const userpage = await axios({
                url: `${apiBase}/users/userpage`,
                params: {
                    id: this.id
                }
            }).then(res => res.data);

            this.userpageRaw = userpage.userpage;
            this.userpage = this.convertBBCode(this.userpageRaw);
        },
        async switchUser() {
            //user
            this.log(
                {
                    required: {
                        rx: this.rxStat,
                        id: this.id
                    },
                    current:
                        Object.entries(this.autoToggleRxUser).length > 0
                            ? this.autoToggleRxUser
                            : "lazy-load"
                },
                "select-user",
                {
                    deep: 2
                }
            );
            if (this.id !== this.autoToggleRxUser.id) {
                await this.initUser();
                await this.initUserpage();
            }
        },
        convertBBCode(bbcode) {
            bbcode = bbcode.split("\n").join("<br>");
            bbcode = this.replaceEmoji(bbcode);
            bbcode = bbCodeParser.parse(bbcode);
            return bbcode;
        },
        replaceEmoji(html) {
            Object.entries({
                ":peppy:": '<img src="./static/emotes/peppy.png">',
                ":barney:": '<img src="./static/emotes/barney.png">',
                ":akerino:": '<img src="./static/emotes/akerino.png">',
                ":foka:": '<img src="./static/emotes/foka.png">',
                ":kappy:": '<img src="./static/emotes/kappy.png">',
                ":creepypeppy:": '<img src="./static/emotes/creepypeppy.png">',
                ":peppyfiero:": '<img src="./static/emotes/peppyfiero.png">',
                ":djpeppy:": '<img src="./static/emotes/djpeppy.png">',
                ":kappa:": '<img src="./static/emotes/kappa.png">'
            }).map(
                ([occur, replace]) => (html = html.split(occur).join(replace))
            );
            return html;
        },
        toStrMode(numericMode = this.numericMode) {
            switch (numericMode) {
                case 0:
                    return "std";
                case 1:
                    return "taiko";
                case 2:
                    return "ctb";
                case 3:
                    return "mania";
                default:
                    break;
            }
        },
        smallerFloatHtml(str) {
            const splitted = str.split(".");
            return `${splitted[0]}<small>.${splitted[1]}</small></span>`;
        }
    }
};
</script>
