import Vuex from 'vuex'
const shit = {};
shit.install = function (Vue, options) {
    Vue.use(Vuex);
    const store = new Vuex.Store({
        state: {
        },
        mutations: {
            save(state, { item, payload }) {
                state[item] = { ...payload }
            },
            notice(){
            }
        },
        actions: {
            async update({ commit, state }, { item, fn }) {
                const payload = await fn(state[item]);
                return commit('save', { item, payload });
            }
        }
    })

    Vue.mixin({
        methods: {
            getShit(item) {
                return this.$sb.get(item);
            },
            storeShit(item, value) {
                return this.$sb.set(item, value);
            },
            asyncStoreShit(item, fn) {
                return this.$sb.cbSet(item, fn);
            },
            notice(){
                store.commit('notice');
            }
        },
        store
    })

    Vue.prototype.$sb = {
        get(item) {
            return store.state[item];
        },
        set(item, newValue) {
            return store.commit('save', { item, payload: newValue });
        },
        cbSet(item, fn) {
            return store.dispatch('update', { item, fn })
        }
    }
}
export default shit;