<template>
    <card no-body shadow>
        <b-table
            :items="scores"
            :fields="fields"
            borderless
            striped
            responsive
            show-empty
            table-variant="secondary"
            class="mb-0 text-nowrap"
            tdClass
        >
            <template v-slot:cell(index)="data">{{ data.index + 1 }}</template>
            <template v-slot:cell(raw_date)="data">
                <span>{{data.value}}</span>
            </template>
            <template v-slot:cell(score)="data">
                <span class="mr-1">{{humanFormat(parseInt(data.value))}},</span>
                <span
                    v-html="`${smallerFloatHtml(data.item.accuracy.toLocaleString('en', {style: 'percent',minimumSignificantDigits: 2, maximumSignificantDigits: 4}))},`"
                    class="mr-1"
                ></span>
                <span v-html="htmlCounts(data.item.counts)"></span>
            </template>
            <template v-slot:cell(user)="data">
                <div class="d-flex">
                    <lazy-image
                        :src="`https://a.ppy.sb/${data.value.id}`"
                        avatar="true"
                        style="width:30px; height:auto"
                        class="mr-2"
                    />
                    <span>{{ data.value.name }}</span>
                </div>
            </template>
        </b-table>
    </card>
</template>
<script>
import LazyImage from "./LazyImage";
const osu = require("node-osu");
const humanFormat = require("human-format");

import modHelper from "@/helpers/enum.js";
export default {
    components: {
        LazyImage
    },
    data() {
        return {
            logger: false,
            fields: [
                {
                    label: "#",
                    key: "index"
                },
                {
                    label: "User",
                    key: "user"
                },
                // "User",
                // { label: "Score", key: "score" },
                { label: "Score", key: "score" },
                { label: "Combo", key: "maxCombo" },
                {
                    label: "Played At",
                    key: "raw_date",
                    sortable: true,
                    formatter: (v, k, i) => {
                        const t = this.moment(new Date(v));
                        return `${t.format("l")} ${t.format("LT")}`;
                    }
                },
                {
                    label: "pp",
                    key: "pp",
                    sortable: true,
                    formatter: k => k.toFixed(2)
                },
                {
                    label: "Mods",
                    key: "raw_mods",
                    formatter: v => modHelper.toModList(v).join(" ")
                }
            ]
        };
    },
    props: ["scores"],
    mounted() {
        this.init();
    },
    computed: {
        bid() {
            return this.beatmap.id;
        }
    },
    methods: {
        async init() {
            this.log(this.fields, "fields");
        },
        smallerFloatHtml(str) {
            const splitted = str.split(".");
            return `${splitted[0]}<small class="float-number">.${splitted[1]}</small></span>`;
        },
        htmlCounts(counts) {
            const c = [
                // { count: this.score.count_300, append: "x 300" },
                { count: counts[100], append: "x 100" },
                { count: counts[50], append: "x 50" },
                { count: counts.miss, append: "Miss" }
            ]
                .filter(({ count }) => count)
                .map(
                    ({ count, append }) =>
                        `${count} <small class="float-number">${append}</small>`
                )
                .join(", ");
            return c;
        },
        humanFormat(...any) {
            return humanFormat(...any);
        }
    }
};
</script>

