import VueJsonPretty from "vue-json-pretty";
const safeJSONStringify = require('safe-json-stringify');

const Toaster = {};
Toaster.install = function (Vue, options) {
    Vue.mixin({
        components: {
            VueJsonPretty,
        },
        methods: {
            createTitle(message) {
                return <strong class="mr-2">{message}</strong>
            },
            createJSONViewer(message, { highlightMouseoverNode, deep } = {}) {
                return <vue-json-pretty data={message} highlightMouseoverNode={highlightMouseoverNode} deep={deep}></vue-json-pretty>;
            },
            popToast(vNodesMsg, vNodesTitle, { variant = "info", toster = undefined } = {}) {
                // Pass the VNodes as an array for message and title
                return this.$bvToast.toast([vNodesMsg], {
                    title: [vNodesTitle],
                    solid: true,
                    variant: variant,
                    toster
                });
            },
            async log(e, title = "result", { variant = "info", toast = undefined, highlightMouseoverNode = true, deep = 2, } = {}) {
                // return;
                if (!this.logger) return;
                return this.popToast(
                    this.createJSONViewer(e, { highlightMouseoverNode, deep }),
                    this.createTitle(title),
                    {
                        variant,
                        toast,

                    }
                );
            },
            safeJSONStringify,
        }
    })
    //     // 1.  property
    //     Vue.myGlobalMethod = function () {
    //         // ...
    //     }

    //     // 2. 
    //     Vue.directive('my-directive', {
    //         bind(el, binding, vnode, oldVnode) {
    //             // ...
    //         }
    //     ...
    //   })

    //     // 3. 
    //     Vue.mixin({
    //         created: function () {
    //             // ...
    //         }
    //     ...
    //   })

    // // 4. 
    // Vue.prototype.$myMethod = function (methodOptions) {
    //     // ...
    // }
}

export default Toaster;